import {Component, OnInit} from '@angular/core';
import {Ichangepasswordform} from '../../../../../Models/Interfaces/change-password/changePasswordform.model';
import {ChangePasswordService} from '../../../../../Services/ChangePassword/change-password.service';
import {ToastrService} from '../../../../../../../../../shared/shared-services/toastr.service';
import {IchangePassword} from '../../../../../../../../../shared/shared-interfaces/change-password.model';
import {ServiceNameEnum} from '../../../../../../../../../shared/shared-enums/ServiceNameEnum';
import {ERequestTypes} from '../../../../../Models/Enums/RequestTypes/RequestTypes';
import {ResponseCodeEnum} from '../../../../../../../../../shared/shared-enums/ResponseCodeEnum';
import {Router} from '@angular/router';
import {LoginService} from '../../../../../Services/AuthServices/LoginService/login.service';
import {OrderListService} from '../../../../../Services/OrderServices/OrderListService/order-list.service';

@Component({
  selector: 'app-password',
  templateUrl: './password.component.html',
  styleUrls: ['./password.component.css']
})
export class PasswordComponent implements OnInit {
  objData!: Ichangepasswordform;
  /**
   * The Requirements For The Construction Of The Component
   * @param service For using changPasswordService methods
   * @param toastService for requests notifications
   * @param loginService
   * @param orderListService
   */
  constructor(private service: ChangePasswordService, private toastService: ToastrService,  private loginService: LoginService, private orderListService: OrderListService) {
  }

  ngOnInit(): void {
  }
  /**
   * onSubmit change Password
   * @param event change password object
   * @return void
   */
  onSubmit(event: IchangePassword): void {
    this.objData = {
      newPassword: event.newPassword,
      oldPassword: event.oldPassword,
      serviceName: ServiceNameEnum.CHANGE_PASSWORD,
      columns: [],
      ConditionsAnded: false
    };

    this.service.changePassword({services: [this.objData], requestType: ERequestTypes.UPDATE}).subscribe(res => {
      if (res.responsCode === ResponseCodeEnum.OK) {
        this.toastService.updateAlert('Changed Successfully', 'Password', 'start');
        this.loginService.logout();
        this.orderListService.clearSearchData();
      }
    });
  }


}
