import {Injectable} from '@angular/core';
import {APIName} from 'shared/API/APIName';
import {IApiData} from '../../Models/Interfaces/APIs/apiData.model';
import {Ichangepasswordform} from '../../Models/Interfaces/change-password/changePasswordform.model';
import {DataService} from '../DataService/data.service';
import {Observable} from 'rxjs';
import {IMainResponse} from '../../Models/Interfaces/Main/MainResponse';

@Injectable({
  providedIn: 'root'
})
export class ChangePasswordService {
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
  constructor(private dataService: DataService) {

  }
  /**
   * change password
   * @param data changed password object
   * @return Observable<IMainResponse>
   */
  changePassword(data: IApiData<Ichangepasswordform>): Observable<IMainResponse> {
    return this.dataService.post(APIName.changePassword, data);
  }
}
